.app {
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: 100vh;
  padding-top: 20px;
  background-color: whitesmoke;
  font-weight: bold;
}

.header {
  width: 90%;
  text-align: center;
  margin-bottom: 20px;
  font-weight: bold;
  font-size: 1.5em;
}

.countdown-bar {
  width: 100%;
  height: 20px;
  border-radius: 10px;
  background-color: lightgray;
  transition: width 0.2s ease;
}


.game-board-container {
  display: flex;
  justify-content: center;
  margin-bottom: 20px;
}

.game-board {
  display: grid;
  gap: 10px;
  max-width: 90vw;
}

.message {
  font-size: 1.5em;
  color: black;
  margin: 10px auto;
  max-width: 80%;
  text-align: center;
}

.next-stage-btn {
  margin-top: 20px;
  padding: 12px 24px;
  font-size: 1.2em;
  background-color: black;
  color: white;
  border: none;
  cursor: pointer;
  border-radius: 8px;
  max-width: 200px;
  width: 80%;
  text-align: center;
}