/* src/Congratulations.css */
.congratulations {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
    text-align: center;
}

.congratulations h1 {
    font-size: 2em;
    margin-bottom: 20px;
}

button {
    font-size: 1.2em;
    padding: 10px 20px;
    border: none;
    border-radius: 8px;
    background-color: black;
    color: white;
    cursor: pointer;
}