/* src/components/GameBoard.css */

.game-board {
    display: grid;
    gap: 10px;
    width: 80vw;
    max-width: 400px;
    margin: 20px auto;
}

.game-cell {
    padding: 30px;
    font-size: 1.5em;
    width: 100%;
    height: 100%;
    background-color: white;
    border: 0.8px solid lightgray;
    font-weight: bold;
    color: black;
    border-radius: 8px;
    cursor: pointer;
    transition: transform 0.1s, border-color 0.3s;
}


button:active {
    position: relative;
    -webkit-tap-highlight-color: transparent;
    -webkit-user-select: none;
    outline: none;
    transform: scale(97%);
}

.game-cell.selected {
    border: 2px solid black;
}

.game-cell.highlighted {
    border: 2px solid red;
}